.wrap {
  max-width: 1200px;
  min-width: 800px;
  padding: 100px;
  margin: auto;
}

.working {
  display: flex;
  gap: 12px;
}

.done {
  display: flex;
  gap: 12px;
}

.list-box {
  width: 200px;
  height: 150px;
  border: 1px solid green;
  border-radius: 10px;
  display: flex;
  /* align-items:center; */
  justify-content: center;
  flex-direction: column;
  padding-left: 48px;
}

.custom-button {
  display: flex;
}
